.popup {
    visibility: hidden;/**/
    opacity: 0;/**/
    transition: visibility, 0.5s ease-in-out;/**/
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.5);
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}
