.header {
    position: relative;
    box-sizing: border-box;
    min-height: 74px;
    width: 100%;
    max-width: 880px;
    padding: 0;
    border-bottom: 1px solid rgba(84, 84, 84, 0.7); /* #545454*; opacity: 0.7;*/
}

@media screen and (max-width: 568px) {
    .header {
        min-height: 56px;
    }
}
